	module.exports = {"data":{"pages":{"internal":{"content":"{\"id\":21,\"title\":\"MEAL Action 8: Focus on Accountability Measures\",\"slug\":\"actions/8\",\"template\":\"action.jsx\",\"category\":\"action\"}"},"childPagesContent":{"internal":{"content":"{\"header\":null,\"body\":{\"0\":{\"type\":\"section\",\"content\":[{\"text\":\"<p>Accountability in practice as defined by SC encompasses the following aspects:&nbsp;</p>\\n<ul>\\n<li><strong>Regular, timely and accessible information sharing.</strong> This includes information about the organization, the projects (including budgetary information), behaviour people can expect of staff and representatives (this should be consistent with child safeguarding policies and codes of conduct), and how people can be involved and provide feedback and complaints. This also involves adjusting information sharing practices to ensure they are systematic and standardised, and involving tailored methods and tools for various audiences.&nbsp;</li>\\n<li><strong>Ensuring participation of communities and children. </strong>This involves discussing, deciding, and working together with children, communities and partners. It entails working with key stakeholders across the programme cycle, moving from consultation to collaboration, and improving quality and inclusiveness of participation where necessary.&nbsp;</li>\\n<li><strong>Proactively seeking feedback from children and communities and handling complaints. </strong>This involves systematic listening, and setting up formal mechanisms within projects for people to express views and concerns on the approach, activities and impact, as well as on safety issues and staff behaviour.&nbsp;</li>\\n<li><strong>Monitoring, evaluating and learning. </strong>This involves giving voice to children and communities in monitoring and evaluation exercises, regularly reviewing monitoring data and data from complaints and feedback mechanisms to inform changes in implementation, and ensuring evaluation data informs future project design, thus building a culture of learning and continual improvement.&nbsp;</li>\\n<li><strong>Building staff competencies for accountable programming. </strong>This involves ensuring that staff have the technical and behavioural competencies to deliver commitments to communities. It also involves working with programme and partner staff to ensure they understand: what accountability looks like in practice; how to implement transparent, participatory programmes; how to set up and manage complaints mechanisms and to abide by relevant code of conduct and child safeguarding procedures.&nbsp;</li>\\n</ul>\\n<p>Different groups of children should be able to access child-sensitive accountability mechanisms; this includes girls and boys of different ages, of different social statuses (e.g. orphans) and children from extremely marginalised groups. This may require project teams to establish multiple channels for communicating with children and their caregivers and receiving complaints and feedback (e.g. focus group discussions, specific play/drawing based activities, having children select a programme staff or community member to share concerns confidentially).&nbsp;&nbsp;</p>\\n\",\"type\":\"paragraph\"}]}}}"}}}},"pathContext":{"slug":"actions/8"}}

